
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import base64 from 'vux/src/tools/base64/index.js'

import { mapMutations } from 'vuex'
export default {
name: 'Login',
data() {
return {
btnText: '发送验证码',
disabled: false,
time: 0,
phoneNumber: '',
verifyCode: '',
smsCode: ''
}
},
created() {
localStorage.removeItem('token')
localStorage.removeItem('userInfo')
localStorage.removeItem('cartData')
this.UPDATE_USERINFO({
userInfo: null
})
},
methods: {
...mapMutations(['UPDATE_USERINFO']),
sendCode() {
const reg = /^1[34578]\d{9}$/ 
if (!this.phoneNumber) {
this.$vux.toast.text('请输入手机号~', 'middle')
return
}
if (!reg.test(this.phoneNumber)) {
this.$vux.toast.text('手机号格式不正确~', 'middle')
return
}
this.time = 60
this.disabled = true
this.timer()

this.$http
.post(
'https://www.easy-mock.com/mock/5a4896ba62de717d44f2406e/api/v1/sendSms',
{ phoneNumber: this.phoneNumber }
)
.then(res => {
let smsCode = res.data.data.verifCode
this.smsCode = smsCode
this.$vux.alert.show({
title: '验证码',
content: `验证码已发送,【${smsCode}】,10分钟有效`
})
setTimeout(() => {
this.$vux.alert.hide()
}, 3000)
})
},
timer() {
if (this.time > 0) {
this.time--
this.btnText = this.time + 's后重新获取'
setTimeout(this.timer, 1000)
} else {
this.time = 0
this.btnText = '获取验证码'
this.disabled = false
}
},
onLogin() {
var reg = /^1[34578]\d{9}$/ 
if (!this.phoneNumber) {
this.$vux.toast.text('手机号不能为空~', 'middle')
return
}
if (!reg.test(this.phoneNumber)) {
this.$vux.toast.text('手机号格式不正确~', 'middle')
return
}
if (!this.verifyCode) {
this.$vux.toast.text('验证码不能为空~', 'middle')
return
}
if (this.verifyCode !== this.smsCode) {
this.$vux.toast.text('验证码不正确~', 'middle')
return
}
const { phoneNumber, verifyCode } = this
const data = {
phoneNumber,
verifyCode
}
this.$http
.post(
'https://www.easy-mock.com/mock/5a4896ba62de717d44f2406e/api/v1/login',
data
)
.then(res => {

if (res.data.data.code === 0) {
this.$vux.toast.show({
text: '登录成功'
})




localStorage.token = base64.encode(res.data.data.token)
localStorage.userInfo = JSON.stringify(
res.data.data.userInfo
)
this.UPDATE_USERINFO({
userInfo: res.data.data.userInfo
})

let redirect = decodeURIComponent(
this.$route.query.redirect || '/'
)
setTimeout(() => {
this.$router.push({
path: redirect
})
}, 2000)
}
})
}
},
components: {
XInput,
XButton,
Group,
Cell,
Toast
}
}



